@import url('https://fonts.googleapis.com/css2?family=Imperial+Script&family=Noto+Sans+Mono:wght@400;700&family=Nunito&family=Nunito+Sans:opsz@6..12&family=Pinyon+Script&family=Praise&family=Raleway:wght@400;700&display=swap')
*
    margin: 0
    padding: 0
html
    height: 100%
body
    height: 100%
    touch-action: none
    // background-color: gray
    -webkit-touch-callout: none
    -webkit-user-select: none
    -khtml-user-select: none
    -moz-user-select: none
    -ms-user-select: none
    user-select: none
    font-family: 'Raleway', sans-serif
    font-family: 'Nunito Sans', sans-serif
    font-family: 'Nunito', sans-serif
    font-family: 'Noto Sans Mono', monospace
    display: flex
    color: #222
    #root
        display: flex
        .App
            display: flex
            flex-direction: column
            // background-color: pink
            h1
                height: 40px
                display: flex
                align-items: center
                justify-content: center
                font-family: 'Imperial Script', cursive
                position: fixed
                width: 100%
                // font-family: 'Noto Sans Mono', monospace
                // font-family: 'Nunito', sans-serif
                // font-family: 'Nunito Sans', sans-serif
                // font-family: 'Pinyon Script', cursive
                // font-family: 'Praise', cursive
                // font-family: 'Raleway', sans-serif
                font-weight: normal
                font-size: 24px
                display: none
            #mainText
                // background-color: tomato
                // line-height: 2.5vh
                // height: calc(80% - 40px)
                height: 70%
                width: 100%
                // position: absolute
                box-sizing: border-box
                overflow-y: scroll
                // padding: 10px 10px 10px 10px
                scrollbar-width: 0
                // display: flex
                justify-content: stretch
                flex-wrap: wrap
                align-content: flex-start
                font-size: 14px
                -ms-overflow-style: none
                scrollbar-width: none
                line-height: 25px
                
                &::-webkit-scrollbar
                    display: none
                pre
                    touch-action: none
                    display: inline-block
                    // background-color: #fff
                    // box-shadow: 0 0 10px #00000011
                    // border-radius: 100px
                    // touch-action: none
                    // display: inline-block
                    // background-color: tan
                    // padding-right: 9.61px
                    // padding: 0 5px
                    // height: 5%
                    font-size: 14px
                    font-family: 'Noto Sans Mono', monospace

                    line-height: 25px
                    // flex: 1
                    // white-space: nowrap
                    // background-color: pink
                    em
                        display: inline-block

            .buttons
                // opacity: 0
                // pointer-events: none
                transition: 0.3s
                text-align: center
                // background-color: gold
                display: flex
                flex-wrap: wrap
                justify-content: center
                // position: fixed
                bottom: 0
                left: 0
                width: 100%
                // background-color: gold
                background-color: #fff
                border-top: 2px solid #eee
                height: 30%
                p
                    padding: 10px
                    width: 100%
                    // height: 20%
                    // background-color: gold !important
                    text-align: left
                button
                    height: 40px
                    // height: 5%
                    padding: 0 30px
                    border: none
                h6
                    font-weight: normal
                    line-height: 40px
                    padding: 0 10px
